// CSS RESET

/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role="list"],
ol[role="list"] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}

// Google font import
@import url("https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,400;0,700;1,700&family=Open+Sans:ital,wght@0,300;0,400;0,600;0,700;1,300;1,400;1,600;1,700&display=swap");

// Custom variables

$font-header: "Nunito", sans-serif;
$font-main: "Open Sans", sans-serif;

$color-blue: #4d8cf5;

// Utility classes

// General styles
body {
  font-family: "Open Sans", sans-serif;
  color: #5c5c5c;
  font-size: 15px;
  line-height: 22px;
  font-weight: 400;
}

h1 {
  margin-top: 20px;
  margin-bottom: 10px;
  font-family: Nunito, sans-serif;
  color: #333;
  font-size: 36px;
  line-height: 44px;
  font-weight: 700;
}

h2 {
  margin-top: 20px;
  margin-bottom: 10px;
  font-family: Nunito, sans-serif;
  color: #333;
  font-size: 28px;
  line-height: 36px;
  font-weight: 700;
}

h3 {
  margin-top: 20px;
  margin-bottom: 10px;
  font-family: Nunito, sans-serif;
  color: #333;
  font-size: 20px;
  line-height: 28px;
  font-weight: 700;
}

h4 {
  margin-top: 10px;
  margin-bottom: 5px;
  font-family: Nunito, sans-serif;
  color: #333;
  font-size: 16px;
  line-height: 22px;
  font-weight: 700;
}

p {
  margin-bottom: 20px;
}

a {
  -webkit-transition: all 300ms ease-in-out;
  transition: all 300ms ease-in-out;
  color: $color-blue;
  font-weight: 600;
  text-decoration: none;
}

// Hero Section

.hero {
  background-image: linear-gradient(
      180deg,
      rgba(0, 0, 0, 0.75),
      rgba(0, 0, 0, 0.75)
    ),
    url(../images/hero.jpg);
  background-size: auto cover;
  background-position: 0px 0px, 50% 50%;
  background-repeat: repeat, no-repeat;
  .wrapper {
    .hero-container {
      display: flex;
      flex-direction: column;
      height: 770px;
      .nav-container {
        display: flex;
        justify-content: space-between;
        .logo {
        }
        .nav-toggle {
          width: 40px;
          height: 40px;
        }
        .nav-wrapper {
          .mobile-nav {
            display: none;
          }
        }
      }
    }
  }
}
